.mobile-message{
    display: none;
}
@media only screen and (max-width: 900px) {
   .mobile-message{
       display: block;
       position: fixed;
       top: 0;
       left: 0;
       width: 100%;
       height: 100vh;
       background-color: black;
       z-index: 1000000;
   }
}