body{
    overflow: hidden;
}

.svg-font, .icon {
    width: 1rem;
    height: 1rem;
    fill: currentColor;
}

.nice-scroll-bar::-webkit-scrollbar-track, .CodeMirror-vscrollbar::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: transparent;
    border-radius: 10px;
}

.nice-scroll-bar::-webkit-scrollbar, .CodeMirror-vscrollbar::-webkit-scrollbar {
    width: 10px;
    background-color: rgb(73, 73, 73);
}

.nice-scroll-bar::-webkit-scrollbar-thumb, .CodeMirror-vscrollbar::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: gray;
}

.scrollable-y {
    overflow-y: auto;
}

.code-animator, .code-viewer, .code {
    height: 100%;
}

.code{
    text-align: left;
}

.code-animator .containers {
    height: 100vh;
}

.code-animator .containers {
    background: rgb(99, 99, 99);
    /* display: grid; */
    /* grid-template-columns: repeat(2, 1fr); */
    overflow: hidden;
}

.containers {
    display: grid;
    grid-template-columns: auto 250px;
}


.CodeMirror, .code-viewer {
    background-color: rgb(50, 50, 50);
    color: white;
    height: 100%;
}

.CodeMirror pre.CodeMirror-line, .code {
    padding-top: 0px;
    padding-right: 4px;
    padding-bottom: 0px;
    padding-left: 4px;
}

.CodeMirror-gutters, .code-line-references {
    background-color: rgb(30, 30, 30);
    border-right: 1px solid #ddd;
}

.CodeMirror-lines,.code-viewer .code{
    padding-top: 4px;
    padding-bottom: 4px;
}

.CodeMirror pre, .code-viewer pre {
    font-family: monospace !important;
    font-size: 1em;
    font-feature-settings: "calt";
}

.code-viewer {
    display: grid;
    grid-template-columns: 31px auto;
    text-align: left;
    /* padding-left: 5%; */
    color: white;
    overflow: auto;
}

.react-codemirror2 {
    height: 100%;
    min-height: 0;
    z-index: 0;
}

.CodeMirror-line {
    text-align: left;
}

.cm-line_comment, .cm-block_comment,  .cm-comment, .cm-block_comment_start, .cm-block_comment_end {
    color: green !important;
}

.cm-identifier {
    color: lightblue !important;
}

.cm-number, cm-float {
    color: lightgreen !important;
}

.cm-keyword {
    color: rgb(207, 0, 197) !important;
}

.cm-binary_op {
    color: rgb(219, 255, 59) !important;
}

.cm-string {
    color: rgb(255, 156, 89) !important;
}

.cm-error {
    color: rgb(255, 0, 0) !important;
}

.cm-true {
    color: greenyellow !important;
}

.cm-false {
    color: red !important;
}

.code {
    /* font-family: Arial, Helvetica, sans-serif; */
    /* cursor: default; */
    display: inline-block;
    position: relative;
    margin: 0;
    color: white;
    overflow: visible;
}

.lowlighted {
    opacity: 0.1 !important;
    transition-duration: 0.5s;
}

.highlighted {
    opacity: 1;
    transition-duration: 0.5s;
}

.view-blocker {
    background-color: white;
    position: absolute;
    /* display: block; */
    box-shadow: 1px 1px 2px rgb(255, 255, 255), 0 0 10px rgb(255, 255, 255), 0 0 5px rgb(255, 255, 255);
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 5px;
}

.handler {
    cursor: grab;
}


.playback-buttons{
    padding-left: 30%;
    padding-right: 30%;
}

.controls-title {
    color: white;
    font-weight: bolder;
    padding: 0;
    margin: auto;
}

.speed-control {
    display: grid;
    grid-template-columns: 1fr 4fr 1fr;
    width: 100%;
}

.speed-icon {
    display: inline-block;
}

.pos-rel{
    position: relative;
}

.overf-x-auto{
    overflow-x: auto;
}

.height-100{
    height: 100%;
    min-height: 0;
}

.rotate-180 {
    transform: rotate(180deg);
}

.rotate-90 {
    transform: rotate(90deg);
}

