.controls-container .btn-container {
    padding: 20px;
    width: 20%;
    display: inline-block;
} */

.if-interrogation-sign {
    font-size: 2rem;
}

.minimize-trigger {
    color: white;
    position: relative;
    z-index: 3000;
    background-color: rgb(83, 83, 83);
    padding: 10px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    opacity: 0.5;
}

.minimize-trigger:hover {
    opacity: 1;
}

.scopes-wrapper {
    pointer-events: none;
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    height: 100%;
    padding-left: 50%;
    overflow: hidden;
}

.scopes-wrapper * {
    pointer-events: all;
}

/* .scopes-wrapper.hidden{
    width: 0%;
}
.scopes-wrapper.show{
    width: 100% !important;
} */

.scopes-container {
    height: 100%;
    padding: 30px;
    border-radius: 10px;
    position: relative;
    right: -100%;
    background-color: rgb(43, 43, 43);
    /* padding-bottom: 800px; */
    /* overflow: auto; */
    min-width: 280px;
}

.scopes-container.show {
    transition-duration: 0.5s;
    right: 70px !important;
}

.controls-container.show {
    transition-duration: 0.5s;
    display: block;
}

.scopes-container.hidden {
    transition-duration: 0.5s;
    right: -100% !important;
}

.scope-var {
    /* border-style: solid; */
    /* background: linear-gradient(138deg, rgba(126,119,255,1) 0%, rgba(68,68,152,1) 35%, rgba(173,173,173,1) 100%); */
    background: linear-gradient(-30deg, rgb(90, 90, 90) 0%, rgb(44, 44, 44) 100%);
    padding: 10px;
    /* padding-top: 10px; */
    /* padding-bottom: 10px; */
    color: white;
}

.dummy-scope-var {
    /* border-style: solid; */
    /* background: linear-gradient(138deg, rgba(126,119,255,1) 0%, rgba(68,68,152,1) 35%, rgba(173,173,173,1) 100%); */
    width: 100%;
}

.drag-to-scope .scope-var {
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 5px;
}

.search-icon {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
    /* background-color: red; */
}

.scope-value {
    position: relative;
    /* width: 100%; */
    /* background-color: red; */
}

.value-expand-trigger {
    cursor: pointer;
    animation-name: blink;
    animation-duration: 0.5s;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    display: inline;
}

/* .value-expand-trigger::after{
    content: "fff";
} */

@keyframes blink {
    from {
        color: rgba(255, 255, 255, 1);
    }
    to {
        color: rgba(255, 255, 255, 0);
    }
}

