.tippy-box[data-theme~='expanded-value'] {
    background-color: rgb(39, 39, 39);
}

.tippy-box[data-theme~='expanded-value'][data-placement^='top']>.tippy-arrow::before {
    border-top-color: white;
}

.tippy-box[data-theme~='expanded-value'][data-placement^='bottom']>.tippy-arrow::before {
    border-bottom-color: white;
}

.tippy-box[data-theme~='expanded-value'][data-placement^='left']>.tippy-arrow::before {
    border-left-color: white;
}

.tippy-box[data-theme~='expanded-value'][data-placement^='right']>.tippy-arrow::before {
    border-right-color: white;
}

[data-tippy-root] {
    display: block;
    float: left;
}

.key-element {
    display: inline;
}

.number-key {
    color: lightgreen
}

.key-element::after {
    content: ":";
    margin-right: 10px;
}

